/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

:root {
  --active-color: #dff4ff;
  --active-miss-color: #61c6ff;
  --hit-color: #e7091e;
  --inactive-color: #a19db0;
  --inactive-miss-color: #999fb5;
  --ship-background-color: #d1d2de;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  font-family: 'robotoregular', 'Courier New', Courier, monospace;
  min-height: 100vh;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

#root,
#__next {
  isolation: isolate;
}

header {
  font-weight: bold;
  background-color: #eeeff0;
  padding: 16px 32px;
}

.container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  margin-top: 50px;
}

.left-board,
.right-board {
  display: grid;
  grid-template-columns: repeat(11, 50px);
  gap: 4px;
}

.left-board > button,
.right-board > button {
  width: 50px;
  height: 50px;
}

.active > .player {
  background-color: #e7091e;
}

.inactive > .player {
  background-color: #a19db0;
}

.player {
  color: white;
  font-weight: bold;
  padding: 16px 0px;
  text-align: center;
  letter-spacing: 0.4rem;
  border-radius: 4px;

  margin-bottom: 32px;
}

.left-board > p,
.right-board > p {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 50px;
  font-weight: bold;
}

.container button {
  border: none;
  border-radius: 4px;
}

.active button {
  background-color: var(--active-color);
}

.active button:hover {
  background-color: #bae6fd;
}

.inactive button:hover {
  background-color: #d4d4d8;
}

button {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dot {
  height: 25px;
  width: 25px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}

button.hit > span {
  background-color: var(--hit-color);
}

.active button.miss > span {
  background-color: var(--active-miss-color);
}

.inactive button.miss > span {
  background-color: var(--inactive-miss-color);
}

button.have-ship {
  background-color: var(--ship-background-color);
}

.winner {
  font-weight: bold;
  text-align: center;
  font-size: 1.5rem;
  height: fit-content;
  padding: 16px 0px;
}

.player1,
.player2 {
  transition: all 1s ease;
}

.active {
  transform: scale(1.05);
}

.hidden {
  opacity: 0;
  display: none;
}

.control-btn {
  display: flex;
  justify-content: center;
  gap: 32px;

  margin-top: 32px;
}

.reset,
.random {
  background-color: var(--hit-color);
}

.control-btn button {
  border: none;
  color: white;
  font-weight: bold;
  padding: 12px 16px;
  border-radius: 6px;
}

.button-hidden {
  opacity: 0;
}

.start {
  background-color: #2563eb;
  opacity: 1;
}

button {
  transition: transform 0.5s ease-out;
}

button:hover {
  transform: scale(1.1);
}
